import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useForm, Controller } from "react-hook-form";
import { useAddUser, useImageUpload } from "../../hooks/useAuth";
import {
  Form,
  Input,
  LabelMessage,
  Button,
  Text,
} from "../../components/LoginCard/styles";
import LoginCard from "../../components/LoginCard";

export default function SignupForm() {
  const navigate = useNavigate();
  const { handleSubmit, control, watch } = useForm();
  const { mutate: addUser } = useAddUser();
  const { mutate: uploadImage } = useImageUpload();
  const [message, setMessage] = useState("");
  
  const onSubmit = (formData, e) => {
    const formImg = new FormData();
    formImg.append("image", e.target.profile.files[0]);

    uploadImage(formImg, {
      onSuccess: filename => {
        formData.image = filename;
        addUser(
          { user: formData },
          {
            onSuccess: data => {
              if (data.user) {
                navigate("/");
              }
            },
            onError: err => {
              setMessage(err.response.data.message);
            },
          }
        );
      },
      onErrpr: err => {
        setMessage(err.response.data.message);
      },
    });
  };

  return (
    <LoginCard title={""} btnName={" "} message={message}>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Controller
          name="email"
          control={control}
          defaultValue=""
          render={({ field: { onChange }, fieldState: { error } }) => (
            <Form>
              <Input onChange={onChange} placeholder="" />
              <LabelMessage>{!!error}</LabelMessage>
              <LabelMessage>{error ? error.message : null}</LabelMessage>
            </Form>
          )}
          rules={{
            required: "  .",
            pattern: {
              value: /\S+@\S+\.\S+/,
              message: "  .",
            },
          }}
        />
        <Controller
          name="password"
          control={control}
          defaultValue=""
          render={({ field: { onChange }, fieldState: { error } }) => (
            <Form>
              <Input
                type="password"
                onChange={onChange}
                placeholder=""
              />
              <LabelMessage>{!!error}</LabelMessage>
              <LabelMessage>{error ? error.message : null}</LabelMessage>
            </Form>
          )}
          rules={{
            required: "  .",
            minLength: {
              value: 6,
              message: "6  .",
            },
            deps: ["passwordConfirm"],
          }}
        />
        <Controller
          name="passwordConfirm"
          control={control}
          defaultValue=""
          render={({ field: { onChange }, fieldState: { error } }) => (
            <Form>
              <Input
                type="password"
                onChange={onChange}
                placeholder=" "
              />
              <LabelMessage>{!!error}</LabelMessage>
              <LabelMessage>{error ? error.message : null}</LabelMessage>
            </Form>
          )}
          rules={{
            required: "   .",
            minLength: {
              value: 6,
              message: "6  .",
            },
            validate: value =>
              value === watch("password") ||
              "   .",
          }}
        />
        <Controller
          name="username"
          control={control}
          defaultValue=""
          render={({ field: { onChange }, fieldState: { error } }) => (
            <Form>
              <Input onChange={onChange} placeholder=" " />
              <LabelMessage>{!!error}</LabelMessage>
              <LabelMessage>{error ? error.message : null}</LabelMessage>
            </Form>
          )}
          rules={{ required: "   ." }}
        />
        <Controller
          name="accountname"
          control={control}
          defaultValue=""
          render={({ field: { onChange }, fieldState: { error } }) => (
            <Form>
              <Input onChange={onChange} placeholder="" />
              <LabelMessage>{!!error}</LabelMessage>
              <LabelMessage>{error ? error.message : null}</LabelMessage>
            </Form>
          )}
          rules={{
            required: "Asf",
            pattern: /^[-._a-z0-9]+$/gi,
          }}
        />
        <Controller
          name="intro"
          control={control}
          defaultValue=""
          render={({ field: { onChange }, fieldState: { error } }) => (
            <Form>
              <Input
                onChange={onChange}
                placeholder="   ."
              />
              <LabelMessage>{!!error}</LabelMessage>
              <LabelMessage>{error ? error.message : null}</LabelMessage>
            </Form>
          )}
          rules={{
            required: "   .",
            pattern: {
              value: /^\d{3}\d{3,4}\d{4,}$/,
              message: "  .",
            },
          }}
        />
        <Controller
          name="image"
          control={control}
          defaultValue=""
          render={({ field: { onChange }, fieldState: { error } }) => (
            <Form>
              <Input
                type="file"
                id="profile"
                accept="image/jpg,image/png,image/jpeg,image/gif"
                onChange={onChange}
              />
              <LabelMessage>{!!error}</LabelMessage>
              <LabelMessage>{error ? error.message : null}</LabelMessage>
            </Form>
          )}
          rules={{
            required: "   .",
          }}
        />
        <Form>
          <Button type="submit"></Button>
          <Text onClick={() => navigate("/signin")}>  </Text>
        </Form>
      </form>
    </LoginCard>
  );
}
